.message {
  /* outline: 2px solid blue; */
  width: 1280px;
  margin: 0 auto;
  padding: 10 70 10 10px;
  display: flex;
  flex-direction: column;
  align-items: end;
}

.imageAndText {
  display: flex;
  flex-direction: row-reverse;
  align-items: end;
}

.imageAndText img {
  width: 48px;
  padding-left: 10px;
  
}

.text {
  position: relative; /* Позволяет позиционировать псевдоэлементы относительно этого блока */
  background-color: #007bff; /* Синий фон */
  padding: 8px 15px; /* Внутренние отступы */
  border-radius: 10px; /* Закругленные углы */
  max-width: 527px; /* Максимальная ширина сообщения */
  /* margin: 20px; !* Отступы вокруг блока *! */
  /* margin: 0 10px 0 0; */
  /* margin: auto */
  text-align: right;
}

.text:after {
  content: ""; /* Псевдоэлемент без контента */
  position: absolute; /* Абсолютное позиционирование относительно родителя */
  bottom: 0; /* Располагаем внизу */
  right: -10px; /* Сдвигаем вправо за пределы блока */
  width: 0;
  height: 0;
  border: 20px solid transparent; /* Треугольник из прозрачных границ */
  border-bottom-color: #007bff; /* Цвет треугольника совпадает с цветом блока */
  border-left: 0; /* Убираем правую границу */
}

.name {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  font-weight: 600;
  color: #fff;
}

.messageText {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #fff;
}

.time {
  font-family: "Montserrat", sans-serif;
  font-size: 10px;
  font-weight: bold;
  color: #000000;
}
